.editor-main-panel {
  background-color: rgb(250, 250, 249);
  height: 100%;
  width: 100%;
  display: flex;
  overflow-y: auto; 
  overflow-x: hidden;
  position: relative;
}

.seafile-editor-resize {
  height: 100%;
  width: 3px;
  background-color: transparent;
  position: absolute;
  top: 0;
  cursor: col-resize;
  z-index: 1;
}

.editor-container {
  /* this container is needed to show the scroll bar */
  height: 100%;
  overflow-y: auto;
  flex: 1 1 auto;
  position: relative;
}
.editor {
  min-height: calc(100% - 40px);
  background: #fff;
  padding: 40px 60px;
  margin: 20px 40px;
  border: 1px solid rgb(230,230,221);
  overflow-x: hidden;
}

.rich-editor-container {
  width: 80%;
  flex: 1, 1, auto;
  height: 100%;
  margin: 0 auto; 
}