.upload-localimg .btn {
  margin-left: -1px;
  border: none;
  line-height: 20px;
  margin-right: 5px;
  padding: 0;
  color: #555;
  min-width: 30px;
  height: 30px;
  width: 30px;
}

.upload-localimg-disabled .btn {
  cursor: default;
}

.upload-localimg-hover .btn:hover {
  background-color: #e5e5e5;
}

.upload-localimg-disable .btn {
  color: #999999;
}

.editor-btn-group .image-uploader {
  display: none;
}

.editor-btn-group .image-uploader-label {
  margin-bottom: 0;
  width: 100%;
  padding: 0.25rem 1.5rem;
}

.editor-btn-group .image-uploader-btn {
  padding: 0;
}

.insert-file {
  position: relative;
  display: inline-block;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}
.insert-file input {
  position: absolute;
  right: -7px;
  top: -7px;
  opacity: 0;
  filter: alpha(opacity=0);
  width: 40px;
  height: 40px;
}
.insert-file:hover {
  background-color: #e5e5e5;
}
.insert-file-disabled:hover {
  background-color: #fff;
}
.insert-file-disabled input {
  color: #999;
}
.button-container .toggle-side-panle-icon {
  position: absolute;
}
.rich-menu .side-panel-btn {
  display: none;
}
@media (max-width: 991.8px) {
  .button-container .toggle-side-panle-icon {
    display: none;
  }
  .rich-menu .side-panel-btn {
    display: block;
  }
}