/*set scroll bar*/
.seafile-editor-module ::-webkit-scrollbar{
  width: 8px;
  height: 8px;
}

.iconfont {
  font-size: 13px;
}

.icon-drop-down {
  display: inline-block;
  transform: scale(0.8);
}

.icon-caret-up {
  display: inline-block;
  transform: scale(0.8);
}
 
.seafile-editor-module ::-webkit-scrollbar-button {
  display: none;
}

.seafile-editor-module ::-webkit-scrollbar-thumb {
  background-color: rgb(206, 206, 212);
  border-radius: 10px;
}

.seafile-editor {
  position: fixed;
  width: 100%;
  height: 100%;
  min-width: 960px;
}

.seafile-editor-main {
  height: calc(100% - 96px);
  width: 100%;
}

/* fix plain editor main height */
.plain-editor-main {
  height: calc(100% - 68px);
}
.seafile-editor-topbar {
  width: 100%;
  height: 68px;
  background-color: #fff;
  padding: 0px 10px;
  border-bottom: 1px solid #e5e5e5;
  box-shadow: 0 3px 2px -2px rgba(200,200,200,.15);
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 3;
}


.seafile-rich-editor-topbar {
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #e5e5e5;
  box-shadow: 0 3px 2px -2px rgba(200,200,200,.15);
  user-select: none;
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
}

.seafile-rich-editor-topbar .use-help-icon {
  background-color: #fff;
  box-sizing: border-box;
}

.seafile-editor-side-panel {
  height: 100%;
  background-color: #fff;
  border-left: 1px solid rgb(230,230,221);
  position: relative;
}

.seafile-editor-main-panel {
  height: 100%;
  background-color: rgb(250,250,249);
  overflow-x: hidden;
  display: flex;
  flex: 0 0 1;
  position: relative; /* for seafile-editor-resize */
}

.plain-editor-left-panel {
  height: 100%;
  width: 50%;
  background-color: #fff;
  border-right: 1px solid rgb(230,230,221);
  overflow-y: scroll;
  overflow-x: hidden;
}

.plain-editor-right-panel {
  height: 100%;
  width: 50%;
  background-color: rgb(250,250,249);
  overflow-y: scroll;
  overflow-x: hidden;
}
