.seafile-editor-module .side-panel {
  user-select: none;
  height:100%;
  background-color: #f5f7fa;
}
.seafile-editor-module .side-panel .nav {
  border-bottom: 1px solid #eee;
  padding-top: 10px;
  padding-bottom: 10px;
  min-width: 125px;
  height: 36px;
  flex-wrap: nowrap;
}
.seafile-editor-module .side-panel .nav .nav-item {
  padding: 0 0.75rem;
}
.seafile-editor-module .side-panel .nav .nav-link {
  padding: 0 0.75rem;
  transition: 0.3s color;
  margin-right: 0;
}
.seafile-editor-module .side-panel .nav-link {
  color: #888;
}
.seafile-editor-module .side-panel .nav .nav-link.active {
  color: #f19645;
  border-bottom: 0;
}
.seafile-editor-module .side-panel .nav .iconfont {
  font-weight: 700;
  font-size: 0.875rem;
}
.seafile-editor-module .side-panel-content {
  padding-top: 8px;
  padding-bottom: 8px;
  height: calc(100% - 36px);
  overflow: hidden;
  font-size: 0.937rem;
}

.seafile-editor-module .side-panel-content:hover {
  overflow: auto;
}

.seafile-editor-module .side-panel .issue-container {
  width: 100%;
  padding: 0 10px 10px 10px;
  margin-top: -10px;
}

.issue-card {
  background-color: #fff;
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-radius: 5px;
  padding-left: 5px;
  margin-top: 10px;
  position: relative;
  word-break: break-word;
  box-shadow: 0 5px 5px -2px rgba(200,200,200, 0.5);
}

.issue-card:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  content: '';
  background-color: rgb(217, 130, 43);
  border-radius: 3px 0 0 3px;
}
.issue-card-title {
  font-size: 16px;
  padding: 5px 10px 3px 10px;
}

.issue-card-description {
  padding: 0 10px 5px 10px;
  color: #aaa;
}

.size-panel-no-content {
  width: 100%;
  text-align: center;
  color: #9a9a9a;
  padding-top: 10px;
}

.image-view {
  width: 200px;
  height: 150px;
  position: absolute;
  background-color: #fff;
  z-index: 1004;
  box-shadow: 0 0 10px #aaa;
  border-radius: 3px;
  line-height: 150px;
  overflow: hidden;
  font-size: 0;
  text-align: center;
}

.image-view img {
  max-width: 100%;
  max-height: 100%;
}

.image-view i {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 150px;
  font-size: 30px;
  color: #eb8205;
  -moz-animation: rotate 1.5s ease infinite;
  -webkit-animation: rotate 1.5s ease infinite;
  animation: rotate 1.5s ease infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.seafile-editor-outline {
  margin-right: 1rem;
}

.outline-h2, .outline-h3 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.outline-h2 {
  margin-left: 20px;
  line-height: 2.5;
  color:#364149;
  white-space: nowrap;
  cursor:pointer;
}
.outline-h2:hover {
  color: #eb8205;
}
.outline-h3 {
  margin-left: 40px;
  line-height: 2.5;
  color:#364149;
  white-space: nowrap;
  cursor:pointer;
}
.outline-h3:hover {
  color: #eb8205;
}

.seafile-editor-all-panel .editor-container .article {
  margin: 20px auto;
  max-width: 950px;
}

@media (max-width: 991.8px) {
  .seafile-editor {
    min-width: calc(100% - 40px);
  }
  .seafile-editor-main-panel {
    width: calc(100% - 200px);
  }
  .seafile-editor-side-panel {
    min-width: 200px;
  }
  .editor-container {
    width: 100%;
  }
  .editor-container .editor {
    margin: 20px !important;
    padding: 20px 30px;
  }
}
@media (max-width: 768px) {
  .editor-container .editor {
    margin: 0 !important;
    padding: 10px 15px;
    border: 0;
  }
}
